<template>
  <view class="container">
    <view class="header">
      <text class="title">ÊêúÁ¥¢Ë®≠ÂÇô‰∏≠...</text>
      <text class="subtitle">Ë´ãÁ¢∫‰øùË®≠ÂÇôËôïÊñºÈÖçÂ∞çÊ®°Âºè</text>
    </view>

    <view class="device-list">
      <view
        v-for="device in bluetoothDevices"
        :key="device.deviceId"
        class="device-item"
        @tap="connectDevice(device)"
      >
        <view class="device-info">
          <text class="device-name">{{ device.name }}</text>
          <text class="device-id">{{ device.deviceId }}</text>
        </view>
        <text class="rssi">‰ø°Ëôü: {{ device.RSSI }}dBm</text>
      </view>
    </view>

    <!-- WiFi‰ø°ÊÅØËæìÂÖ•ÂºπÊ°Ü -->
    <uni-popup ref="popup" type="dialog">
      <uni-popup-dialog
        mode="input"
        title="Wi-Fi‰ø°ÊÅØ"
        :before-close="true"
        @close="handleClose"
        @confirm="handleConfirm"
      >
        <view class="wifi-input-container">
          <input
            class="wifi-input"
            v-model="SSID"
            placeholder="Ë´ãËº∏ÂÖ•Wi-FiË≥¨Ëôü"
            type="text"
          />
          <input
            class="wifi-input"
            v-model="Password"
            placeholder="Ë´ãËº∏ÂÖ•Wi-FiÂØÜÁ¢º"
            type="text"
          />
        </view>
      </uni-popup-dialog>
    </uni-popup>
  </view>
</template>

<script setup>
import { ref, onMounted, onUnmounted, nextTick } from "vue";

const popup = ref(null);
const bluetoothDevices = ref([]);
const SSID = ref("");
const Password = ref("");
const isBluetoothInitialized = ref(false);

// Â≠òÂÇ®‰∫ßÂìÅÂØÜÈí•ÂíåËÆæÂ§áÂêçÁß∞
const productKey = ref("");
const deviceSecret = ref("");
const deviceName = ref("");

// ÊòæÁ§∫ WiFi ‰ø°ÊÅØËæìÂÖ•ÂºπÊ°Ü
const showWiFiInput = async () => {
  // Á≠âÂæÖÁªÑ‰ª∂ÊåÇËΩΩÂÆåÊàê
  await nextTick();

  // Ëé∑Âèñ‰πãÂâç‰øùÂ≠òÁöÑ WiFi ‰ø°ÊÅØ
  SSID.value = uni.getStorageSync("wifiSSID") || "";
  Password.value = uni.getStorageSync("wifiPassword") || "";

  // ÊòæÁ§∫ÂºπÊ°Ü
  if (popup.value) {
    popup.value.open();
  }
};

// Â§ÑÁêÜÁ°ÆËÆ§ÊåâÈíÆ
const handleConfirm = () => {
  if (!SSID.value || !Password.value) {
    uni.showToast({
      title: "Wi-FiË≥¨ËôüÂíåÂØÜÁ¢º‰∏çËÉΩÁÇ∫Á©∫",
      icon: "none",
    });
    return;
  }

  // ‰øùÂ≠ò WiFi ‰ø°ÊÅØ
  uni.setStorageSync("wifiSSID", SSID.value);
  uni.setStorageSync("wifiPassword", Password.value);

  // ÂÖ≥Èó≠ÂºπÊ°Ü
  popup.value.close();

  // ÂºÄÂßãËìùÁâôÂàùÂßãÂåñ
  initBluetooth();
};

// Â§ÑÁêÜÂèñÊ∂àÊåâÈíÆ
const handleClose = () => {
  // ËøîÂõû‰∏ä‰∏ÄÈ°µ
  uni.navigateBack();
};

// ÂàùÂßãÂåñËìùÁâôÊ®°Âùó
const initBluetooth = () => {
  uni.openBluetoothAdapter({
    success: (res) => {
      console.log("ËìùÁâôÂàùÂßãÂåñÊàêÂäü:", res);
      isBluetoothInitialized.value = true;
      startBluetoothDevicesDiscovery();
    },
    fail: (error) => {
      console.error("ËìùÁâôÂàùÂßãÂåñÂ§±Ë¥•:", error);
      isBluetoothInitialized.value = false;
      uni.showToast({
        title: "Ë´ãÈñãÂïüËóçÁâô",
        icon: "none",
      });
    },
  });
};

// ÂºÄÂßãÊêúÁ¥¢ËìùÁâôËÆæÂ§á
const startBluetoothDevicesDiscovery = () => {
  uni.startBluetoothDevicesDiscovery({
    success: () => {
      // ÁõëÂê¨ÂØªÊâæÂà∞Êñ∞ËÆæÂ§áÁöÑ‰∫ã‰ª∂
      uni.onBluetoothDeviceFound((res) => {
        console.log("ÂèëÁé∞Êñ∞ËÆæÂ§á:", res);
        res.devices.forEach((device) => {
          if (
            device.name &&
            !bluetoothDevices.value.some((d) => d.deviceId === device.deviceId)
          ) {
            bluetoothDevices.value.push(device);
          }
        });
      });
    },
    fail: (error) => {
      console.error("ÊêúÁ¥¢ËìùÁâôËÆæÂ§áÂ§±Ë¥•:", error);
      uni.showModal({
        title: "ÊêúÁ¥¢Ë®≠ÂÇôÂ§±Êïó" + error.errCode + error.errMsg,
        icon: "none",
      });
    },
  });
};

// ËøûÊé•ËÆæÂ§á
const connectDevice = (device) => {
  uni.createBLEConnection({
    deviceId: device.deviceId,
    success: (res) => {
      uni.showToast({
        title: "ÈÄ£Êé•ÊàêÂäü",
        icon: "success",
      });
      console.log("üöÄ ~ connectDevice ~ res:", res);
      getServices(device);
      // ËøôÈáåÂèØ‰ª•Ê∑ªÂä†ËÆæÂ§áÂà∞ÂêéÁ´Ø
      // uni.navigateTo({
      //   url: "/pages/index/index",
      // });
    },
    fail: (error) => {
      console.error("ËøûÊé•Â§±Ë¥•:", error);
      uni.showToast({
        title: "ÈÄ£Êé•Â§±Êïó",
        icon: "none",
      });
    },
  });
};

// Ëé∑ÂèñÊúçÂä°
const getServices = (device) => {
  uni.getBLEDeviceServices({
    deviceId: device.deviceId,
    success: (res) => {
      console.log("üöÄ ~ getServices ~ res:", res);
      getCharacteristics(device, res.services[0].uuid);
    },
    fail: (error) => {
      console.error("Ëé∑ÂèñÊúçÂä°Â§±Ë¥•:", error);
    },
  });
};

// Ëé∑ÂèñÁâπÂæÅÂÄº
const getCharacteristics = (device, serviceId) => {
  uni.getBLEDeviceCharacteristics({
    deviceId: device.deviceId,
    serviceId,
    success: (res) => {
      console.log("üöÄ ~ getCharacteristics ~ res:", res);
      let writeCharacteristicId = "";
      res.characteristics.forEach((characteristic) => {
        if (characteristic.properties.write) {
          writeCharacteristicId = characteristic.uuid;
        }
      });
      console.log(
        "üöÄ ~ getCharacteristics ~ writeCharacteristicId:",
        writeCharacteristicId
      );
      sendData(device, serviceId, writeCharacteristicId);

      // startNotify(device, serviceId, writeCharacteristicId);
    },
    fail: (error) => {
      console.error("Ëé∑ÂèñÁâπÂæÅÂÄºÂ§±Ë¥•:", error);
    },
  });
};

// ÂºÄÂêØÊ∂àÊÅØÁõëÂê¨
const startNotify = (device, serviceId, characteristicId) => {
  uni.notifyBLECharacteristicValueChange({
    deviceId: device.deviceId,
    serviceId,
    characteristicId,
    state: true,
    success: (res) => {
      console.log("üöÄ ~ startNotify ~ res:", res);
      // Êé•Êî∂Ê∂àÊÅØÁöÑÊñπÊ≥ï
      listenValueChange();
      // ÂèëÈÄÅÊï∞ÊçÆ
      sendData(device, serviceId, characteristicId);
    },
    fail: (error) => {
      console.error("ÂºÄÂêØÊ∂àÊÅØÁõëÂê¨Â§±Ë¥•:", error);
    },
  });
};

// ÁõëÂê¨Ê∂àÊÅØÂèòÂåñ
const listenValueChange = () => {
  uni.onBLECharacteristicValueChange((res) => {
    console.log("üöÄ ~ listenValueChange ~ res:", res);
    // ÁªìÊûúÈáåÊúâ‰∏™valueÂÄºÔºåËØ•ÂÄº‰∏∫ ArrayBuffer Á±ªÂûãÔºåÊâÄ‰ª•Âú®ÊéßÂà∂Âè∞Êó†Ê≥ïÁî®ËÇâÁúºËßÇÂØüÂà∞ÔºåÂøÖÈ°ªÂ∞ÜËØ•ÂÄºËΩ¨Êç¢‰∏∫16ËøõÂà∂
    let resHex = ab2hex(res.value);
    console.log(resHex);
    // ÊúÄÂêéÂ∞Ü16ËøõÂà∂ËΩ¨Êç¢‰∏∫asciiÁ†ÅÔºåÂ∞±ËÉΩÁúãÂà∞ÂØπÂ∫îÁöÑÁªìÊûú
    let result = hexCharCodeToStr(resHex);
    console.log("üöÄ ~ uni.onBLECharacteristicValueChange ~ result:", result);
  });
};

// Â∞Ü ArrayBuffer ËΩ¨Êç¢‰∏∫16ËøõÂà∂
const ab2hex = (buffer) => {
  const hexArr = Array.prototype.map.call(
    new Uint8Array(buffer),
    function (bit) {
      return ("00" + bit.toString(16)).slice(-2);
    }
  );
  return hexArr.join("");
};

// Â∞Ü16ËøõÂà∂ËΩ¨Êç¢‰∏∫asciiÁ†Å
const hexCharCodeToStr = (hexCharCodeStr) => {
  var trimedStr = hexCharCodeStr.trim();
  var rawStr =
    trimedStr.substr(0, 2).toLowerCase() === "0x"
      ? trimedStr.substr(2)
      : trimedStr;
  var len = rawStr.length;
  if (len % 2 !== 0) {
    console("Â≠òÂú®ÈùûÊ≥ïÂ≠óÁ¨¶!");
    return "";
  }
  var curCharCode;
  var resultStr = [];
  for (var i = 0; i < len; i = i + 2) {
    curCharCode = parseInt(rawStr.substr(i, 2), 16);
    resultStr.push(String.fromCharCode(curCharCode));
  }
  return resultStr.join("");
};

// ÂèëÈÄÅÊï∞ÊçÆ
const sendData = (device, serviceId, characteristicId) => {
  const msg = `SSID:${SSID.value};Password:${Password.value};ProductKey:${productKey.value};DeviceName:${deviceName.value};DeviceSecret:${deviceSecret.value};`;
  const buffer = new ArrayBuffer(msg.length);
  const dataView = new DataView(buffer);
  // dataView.setUint8(0, 0)

  for (var i = 0; i < msg.length; i++) {
    dataView.setUint8(i, msg.charAt(i).charCodeAt());
  }
  uni.writeBLECharacteristicValue({
    deviceId: device.deviceId,
    serviceId,
    characteristicId,
    value: buffer,
    success: (res) => {
      console.log("üöÄ ~ sendData ~ res:", res);
      uni.showToast({
        title: "ÂèëÈÄÅÊï∞ÊçÆÊàêÂäü",
        icon: "success",
      });
      setTimeout(() => {
        uni.navigateBack();
      }, 1000);
    },
    fail: (error) => {
      console.error("ÂèëÈÄÅÊï∞ÊçÆÂ§±Ë¥•:", error);
    },
  });
};

// È°µÈù¢Âä†ËΩΩÊó∂ÊâßË°å
onMounted(async () => {
  const pages = getCurrentPages();
  const currentPage = pages[pages.length - 1];

  // Ëé∑ÂèñË∑ØÁî±ÂèÇÊï∞
  productKey.value = decodeURIComponent(currentPage.options.productKey || "");
  deviceSecret.value = decodeURIComponent(
    currentPage.options.deviceSecret || ""
  );
  deviceName.value = decodeURIComponent(currentPage.options.deviceName || "");

  await showWiFiInput();
});

// È°µÈù¢Âç∏ËΩΩÊó∂Ê∏ÖÁêÜËìùÁâô
onUnmounted(() => {
  if (isBluetoothInitialized.value) {
    uni.stopBluetoothDevicesDiscovery();
    uni.closeBluetoothAdapter();
  }
});
</script>

